import * as React from 'react';

export interface DimensionsProps {
    width: number;
    length: number;
    height: number;
    thickness: number;
    onChange?: (width: number, length: number, height: number, thickness: number) => void;
}

export class Dimensions extends React.Component<DimensionsProps> {
    constructor(props: DimensionsProps) {
        super(props);
        this.onChangeWidth = this.onChangeWidth.bind(this);
        this.onChangeLength = this.onChangeLength.bind(this);
        this.onChangeHeight = this.onChangeHeight.bind(this);
        this.onChangeThickness = this.onChangeThickness.bind(this);
    }

    onChangeWidth(event: React.ChangeEvent<HTMLInputElement>) {
        if (!this.props.onChange) return;
        this.props.onChange(event.target.valueAsNumber, this.props.length, this.props.height, this.props.thickness);
    }

    onChangeLength(event: React.ChangeEvent<HTMLInputElement>) {
        if (!this.props.onChange) return;
        this.props.onChange(this.props.width, event.target.valueAsNumber, this.props.height, this.props.thickness);
    }

    onChangeHeight(event: React.ChangeEvent<HTMLInputElement>) {
        if (!this.props.onChange) return;
        this.props.onChange(this.props.width, this.props.length, event.target.valueAsNumber, this.props.thickness);
    }

    onChangeThickness(event: React.ChangeEvent<HTMLInputElement>) {
        if (!this.props.onChange) return;
        this.props.onChange(this.props.width, this.props.length, this.props.height, event.target.valueAsNumber);
    }

    render() {
        return (
            <div>
                <h3>Dimensions</h3>
                <div style={{
                    display: 'grid',
                    width: '20em',
                    gridTemplateColumns: '1fr 1fr',
                    gridRowGap: '1em'
                }}>
                    <div>Width:</div>
                    <div><input type="number" value={this.props.width} onChange={this.onChangeWidth} /></div>

                    <div>Length:</div>
                    <div><input type="number" value={this.props.length} onChange={this.onChangeLength} /></div>

                    <div>Height:</div>
                    <div><input type="number" value={this.props.height} onChange={this.onChangeHeight} /></div>

                    <div>Thickness:</div>
                    <div><input type="number" value={this.props.thickness} onChange={this.onChangeThickness} /></div>
                </div>
            </div>
        )
    }
}