import * as React from 'react';

import {Dimensions, DimensionsProps} from './Dimensions';
import { PizzaBox } from './PizzaBox';
import { SimpleBox } from './SimpleBox';
import { PaperSize, CommonSizes } from './PaperSize';

enum BoxStyle {
    Pizza,
    Simple,
}

const BoxStyles = [BoxStyle[BoxStyle.Pizza], BoxStyle[BoxStyle.Simple]];


interface BoxDesignerState extends DimensionsProps{
    style: BoxStyle;
    paperSize: PaperSize;
}

export default class BoxDesigner extends React.Component<{}, BoxDesignerState> {
    constructor(props: {}) {
        super(props);
        this.state = {
            width: 2.5,
            length: 2,
            height: 1,
            thickness: 0.05,
            style: BoxStyle.Pizza,
            paperSize: CommonSizes[0],
        };

        this.onChangeDimensions = this.onChangeDimensions.bind(this);
        this.onChangeStyle = this.onChangeStyle.bind(this);
        this.onChangePaperSize = this.onChangePaperSize.bind(this);
    }

    onChangeDimensions(width: number, length: number, height: number, thickness: number) {
        this.setState({
            width: width,
            length: length,
            height: height,
            thickness: thickness,
        });
    }

    onChangeStyle(event: React.ChangeEvent<HTMLSelectElement>) {
        this.setState({style: BoxStyle[event.target.value as keyof typeof BoxStyle]});
    }

    onChangePaperSize(event: React.ChangeEvent<HTMLSelectElement>) {
        const size = CommonSizes.find(el => el.name == event.target.value);
        this.setState({paperSize: size});
    }

    render() {
        return (
            <div>
                <h2>Box Designer</h2>
                <div>
                    <h3>Paper Size</h3>
                    <select value={this.state.paperSize.name} onChange={this.onChangePaperSize}>
                        {CommonSizes.map((el, i) => <option key={i}>{el.name}</option>)}
                    </select>
                    {' '}
                    {this.state.paperSize.width.value} × {this.state.paperSize.height.value} {this.state.paperSize.width.unit}
                </div>
                <Dimensions
                    width={this.state.width} length={this.state.length}
                    height={this.state.height} thickness={this.state.thickness}
                    onChange={this.onChangeDimensions} />
                <div>
                    <h3>Style</h3>
                    <select value={BoxStyle[this.state.style]} onChange={this.onChangeStyle}>
                        {BoxStyles.map((el, i) => <option key={i} value={el}>{el}</option>)}
                    </select>
                </div>
                {this.renderDiagram()}
            </div>
        );
    }

    renderDiagram() {
        if  (this.state.style == BoxStyle.Pizza) {
            return <PizzaBox width={this.state.width} length={this.state.length}
             height={this.state.height} thickness={this.state.thickness} paperSize={this.state.paperSize} />
        } else if (this.state.style == BoxStyle.Simple) {
            return <SimpleBox width={this.state.width} length={this.state.length}
             height={this.state.height} thickness={this.state.thickness} paperSize={this.state.paperSize} />
        } else {
            return <div style={{fontStyle:'italic'}}>Unsupported Style</div>
        }
    }
}
