import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { DimensionsProps } from './Dimensions';
import BoxHelper from './BoxHelper';
import { PaperSize } from './PaperSize';

interface SimpleBoxProps extends DimensionsProps {
    paperSize: PaperSize;
}

interface BoxLandmarks {
    ax: number;
    arx: number;
    blx: number;
    bx: number;
    brx: number;
    cx: number;
    cr1x: number;
    crx: number;
    dlx: number;
    dl1x: number;
    dx: number;
    elx: number;
    ex: number;
    erx: number;
    my: number;
    ny: number;
    oy: number;
    oby: number;
    pty: number;
    py: number;
    pby: number;
    qy: number;
    rotate: boolean;
    f: number;
    fs: number;
    bt: number;
}

const kHelper = new BoxHelper('simple-box');

export class SimpleBox extends React.Component<SimpleBoxProps> {
    constructor(props: SimpleBoxProps) {
        super(props);
    }

    dataUri() {
       const raw = ReactDOMServer.renderToStaticMarkup(this.renderSvg(false));
       return kHelper.createDataUri(raw);
    }

    filename() {
        return kHelper.createFileName(this.props);
    }

    render () {
        return (
            <div>
                <h3>Simple Box Template</h3>
                <div>
                    <a href={this.dataUri()} download={this.filename()} type="image/svg+xml">Download</a>
                </div> 
                {this.renderSvg(true)}
            </div>
        );
    }

    /*
                              .----.                M
                             /      \
                            +⋅⋅⋅⋅⋅⋅⋅⋅+              N
                            |        |
                            |        |
            +--------+------+⋅⋅⋅⋅⋅⋅⋅⋅+------+.      O
            |        :      :        :      : \
            |        :      :        :      :  |    b
            |        :      :        :      :  |
            |        :      :        :      :  |    t
            |        :      :        :      : /
            +⋅⋅⋅⋅⋅⋅⋅⋅+⋅⋅⋅⋅⋅⋅+⋅⋅⋅⋅⋅⋅⋅⋅+⋅⋅⋅⋅⋅⋅+       P
            |  ____  |\     |\_    _/|     /        b
            |_/    \_| |____|  |__|  |____|         Q

            A r    l B r    C  r  l  D    l E  r
     */

    generateLandmarks() : BoxLandmarks {
        const w = Math.max(this.props.width, this.props.height);
        const l = Math.min(this.props.width, this.props.length);
        const l2 = l / 2;
        const f = Math.min(1, l2, this.props.height / 2);
        const fs = f * Math.tan(Math.PI / 12);

        const ax = 0;
        const arx = ax + l2;

        const bx = ax + w;
        const blx = bx - l2;
        const brx = bx + l2;

        const cx = bx + l;
        const crx = cx + l2;
        const cr1x = cx + fs;

        const dx = cx + w;
        const dlx = dx - l2;
        const dl1x = dx - fs;


        const ex = dx + l;
        const elx = ex - l2;
        const erx = ex + f;

        const my = 0;
        const ny = my + f;
        const oy = ny + l;
        const oby = oy + fs; // 15 degrees
        const py = oy + this.props.height;
        const pty = py - fs; // 15 degrees
        const pby = py + l2;
        const qy = py + l - this.props.thickness;

        const bt = dlx - crx;

        return {
            ax: ax, arx: arx,
            blx: blx, bx: bx, brx: brx,
            cx: cx, cr1x: cr1x, crx: crx,
            dlx: dlx, dl1x: dl1x, dx: dx,
            elx: elx, ex: ex, erx: erx,
            my: my,
            ny: ny,
            oy: oy, oby: oby,
            pty: pty, py: py, pby: pby,
            qy: qy,
            rotate: erx > qy,
            f: f,
            fs: fs,
            bt: bt,
        };
    }

    renderSvg(display: boolean) {
        const l = this.generateLandmarks();
        const outline = [
           l.ax, l.oy,
           l.bx, l.oy,
           l.bx + l.fs, l.oy - l.f,
           l.cx - l.fs, l.oy - l.f,
           l.cx, l.oy,
           l.cx, l.ny,
           l.cr1x, l.my,
           l.dl1x, l.my,
           l.dx, l.ny,
           l.dx, l.oy,
           l.dx + l.fs, l.oy - l.f,
           l.ex - l.fs, l.oy - l.f,
           l.ex, l.oy,
           l.erx, l.oby,
           l.erx, l.pty,
           l.ex, l.py,
           l.elx, l.pby,
           l.elx, l.pby + l.bt,
           l.dx, l.pby + l.bt,
           l.dx, l.py,
           l.dlx, l.pby,
           l.dlx, l.qy,
           l.crx, l.qy,
           l.crx, l.pby,
           l.cx, l.py,
           l.cx, l.pby + l.bt,
           l.brx, l.pby + l.bt,
           l.brx, l.pby,
           l.bx, l.py,
           l.bx, l.qy,
           l.blx, l.pby,
           l.arx, l.pby,
           l.ax, l.qy,
           l.ax, l.oy
        ];
        const path = kHelper.pathFromOutline(outline);
        const fill = display ? '#eee' : 'none'

        const cuts = (
            <g stroke="red" strokeWidth="0.01" fill="none">
                <path d={path} fill={fill}/>
                <line x1={l.cx} y1={l.ny} x2={l.cx + 2 * l.fs} y2={l.ny} />
                <line x1={l.cx + 2 * l.fs} y1={l.ny} x2={l.cx + 2 * l.fs} y2={l.ny + this.props.thickness} />
                <line x2={l.dx} y1={l.ny} x1={l.dx - 2 * l.fs} y2={l.ny} />
                <line x1={l.dx - 2 * l.fs} y1={l.ny} x2={l.dx - 2 * l.fs} y2={l.ny + this.props.thickness} />
            </g>
        );

        const scores = (
            <g stroke="black" strokeWidth="0.01" fill="none" strokeDasharray="0.05 0.05">
                <line x1={l.cx + 2 * l.fs} y1={l.ny + this.props.thickness}
                      x2={l.dx - 2 * l.fs} y2={l.ny + this.props.thickness} />
                <line x1={l.bx} y1={l.oy} x2={l.ex} y2={l.oy} />
                <line x1={l.ax} y1={l.py} x2={l.ex} y2={l.py} />
                <line x1={l.bx} y1={l.oy} x2={l.bx} y2={l.py} />
                <line x1={l.cx} y1={l.oy} x2={l.cx} y2={l.py} />
                <line x1={l.dx} y1={l.oy} x2={l.dx} y2={l.py} />
                <line x1={l.ex} y1={l.oy} x2={l.ex} y2={l.py} />
            </g>
        );

        const svg_sizes = BoxHelper.sizesFromPaper(this.props.paperSize, l.rotate);
        const style = display ? {border:'1px solid #888'} : null;

        return (
            <svg {...svg_sizes} style={style}>
                {cuts}
                {scores}
            </svg>
        );
    }
}