import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { DimensionsProps } from './Dimensions';
import { PaperSize } from './PaperSize';

const kSvgHeader = '<!DOCTYPE svg PUBLIC "-//W3C//DTD SVG 1.1//EN" "http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd">\n';
const kSvgAttrs = 'version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xml:space="preserve" ';

export default class BoxHelper {
    name: string;

    constructor(name: string) {
        this.name = name;
    }

    createDataUri(input: string): string {
        if (!input.startsWith('<svg ')) {
            return ''
        }
        const src = [kSvgHeader, input.substring(0, 5), kSvgAttrs, input.substring(5)]
        const uri = `data:image/svg+xml;base64,${window.btoa(src.join(''))}`;
        return uri;
    }

    createFileName(dimensions: DimensionsProps) {
        return `${this.name}-${dimensions.width}x${dimensions.length}x${dimensions.height}.svg`;
    }

    round(val:number, places:number) : number {
        const up = Math.round(parseFloat(`${val}e+${places}`));
        const down = parseFloat(`${up}e-${places}`);
        return down;
    }

    pathFromOutline(outline: number[]) : string {
        const accum = ['M'];

        let px = Number.NaN;
        let py = Number.NaN;
        let pc = 'M';

        for (let i = 0; i < outline.length; i+= 2) {
            const x = this.round(outline[i], 3);
            const y = this.round(outline[i + 1], 3);

            if (x == px && y == py) continue;
            if (y == py) {
                if (pc != 'H') accum.push('H');
                accum.push(x.toString());
                px = x;
                pc = 'H'
            } else if (x == px) {
                if (pc != 'V') accum.push('V');
                accum.push(y.toString());
                py = y;
                pc = 'V';
            } else {
                if (i && pc != 'L') accum.push('L')
                accum.push(x.toString());
                accum.push(y.toString());
                px = x;
                py = y;
                pc = 'L';
            }
        }

        accum.push('Z');
        return accum.join(' ');
    }

    bluntCorners(outline: number[], offset: number) : number[] {
        const output : number[] = [];

        for (let i = 2; i < outline.length; i += 2) {
            const x = outline[i];
            const y = outline[i + 1];

            const px = outline[i - 2];
            const py = outline[i - 1];

            const nx = outline[(i + 2) % outline.length];
            const ny = outline[(i + 3) % outline.length];

            const dpx = px - x;
            const dpy = py - y;
            const dplen = Math.sqrt(dpx * dpx + dpy * dpy);
             const ndpx = dpx / dplen;
             const ndpy = dpy / dplen;

            const dnx = nx - x;
            const dny = ny - y;
             const dnlen = Math.sqrt(dnx * dnx + dny * dny);
             const ndnx = dnx / dnlen;
             const ndny = dny / dnlen;


            const dot = ndpx * ndnx + ndpy * ndny;
            if (Math.abs(dot) < 1e-4 && ndpx * ndny - ndpy * ndnx < 0) {
                output.push(x + ndpx * offset);
                output.push(y + ndpy * offset);
                output.push(x + ndnx * offset);
                output.push(y + ndny * offset);
            } else {
                output.push(x);
                output.push(y);
            }
        }

        return output;
    }

    static sizesFromPaper(paperSize: PaperSize, rotate: boolean) {
        const w = paperSize.width.value;
        const h = paperSize.height.value;
        const u = paperSize.width.unit;

        if (rotate) {
            return {
                width: `${h}${u}`,
                height: `${w}${u}`,
                viewBox: `-0.25 -0.25 ${h - 0.5} ${w - 0.5}`
            }
        } else {
            return {
                width: `${w}${u}`,
                height: `${h}${u}`,
                viewBox: `-0.25 -0.25 ${w - 0.5} ${h - 0.5}`
            }
        }
    }
}