import { MeasureValue, Measure } from "./Measure";

export class PaperSize {
  name: string;
  width: MeasureValue;
  height: MeasureValue;

  constructor(name: string, width: MeasureValue, height: MeasureValue) {
    if (width.unit != height.unit) {
      throw new Error('PaperSize requires width and height to have the same units');
    }
    this.name = name;
    this.width = width;
    this.height = height;
  }

  is_metric() {
    return this.width.unit == 'cm';
  }

  static Parse(input: string) {
    const matches = input.match(/(.*)\s+(\d+(?:\.\d+)?)x(\d+(?:\.\d+)?)\s+(cm|in)/)
    if (!matches) {
      return;
    }
    const name = matches[1];
    if (!name) {
      return;
    }
    const width = parseFloat(matches[2]);
    const height = parseFloat(matches[3]);
    const unit = matches[4];
    if (unit != 'cm' && unit != 'in') {
      return;
    }
    return new PaperSize(name, {value:width, unit: unit}, {value: height, unit: unit});
  }
}


export const CommonSizes = [
  "Letter 8.5x11 in",
  "Legal 8.5x14 in",
  "Ledger 11x17 in",
  "B Plus 13x19 in",
  "Cricut Square 12x12 in",
  "Cricut Long 12x24 in",
  "Cricut Square Metric 30x30 cm",
  "Cricut Long Metric 30x60 cm",
  "A4 21x29.7 cm",
  "A3 29.7x42 cm",
  "A2 42x59.4 cm",
  "A1 59.4x84 cm"
].map(el => PaperSize.Parse(el)).filter(el => !!el);