import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { DimensionsProps } from './Dimensions';
import BoxHelper from './BoxHelper';
import { PaperSize } from './PaperSize';

export interface PizzaBoxProps extends DimensionsProps{
    paperSize: PaperSize;
}

interface BoxLandmarks {
    ax: number;
    a1x: number;
    b1x: number;
    b2x: number;
    b3x: number;
    cx: number;
    c1x: number;
    dx: number;
    d1x: number;
    e0x: number;
    e1x: number;
    e2x: number;
    fx: number;
    f1x: number
    gy: number;
    g1y: number;
    h1y: number;
    h2y: number;
    iy: number;
    jy: number;
    ky: number;
    ly: number; 
    my: number;
    rotate: boolean;
}

const kHelper = new BoxHelper('pizza-template');

export class PizzaBox extends React.Component<PizzaBoxProps> {
    constructor(props: PizzaBoxProps) {
        super(props)
        this.dataUri = this.dataUri.bind(this);
        this.filename = this.filename.bind(this);
    }

    dataUri() {
       const raw = ReactDOMServer.renderToStaticMarkup(this.renderSvg(false));
       return kHelper.createDataUri(raw);
    }

    filename() {
        return kHelper.createFileName(this.props);
    }

    render () {
        return (
            <div>
                <h3>Pizza Box Template</h3>
                <div>
                    <a href={this.dataUri()} download={this.filename()} type="image/svg+xml">Download</a>
                </div> 
                {this.renderSvg(true)}
            </div>
        );
    }

   
    /*
    landmarks

      A  B* C             D  E* F

G           +-------------+           0
         +--|             |--+
H*       |  +:::::::::::::+  |        h + th
         |  |             |  |
I     +--+⋅⋅+⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅+⋅⋅+--+     2h + th
      |  :  :             :  :  |
      |  :  :             :  :  |
      |  :  :             :  :  |
      |  :  :             :  :  |
      |  :  :             :  :  |
J     +--+--+⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅+--+--+     2h + l + th
      |     :             :     |
K     +--+--+⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅+--+--+     3h + l + th
         |  :             :  |
         |  :             :  |
         |  :             :  |
         |  :             :  |
         |  :             :  |
L        +--+⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅⋅+--+        3h + 2l + th
            |             |
M           +-------------+           4h + 2l + th
        
            
      0  h  2             2  3  4
         +  h             h  h  h
         t  +             +  +  +
            t             t  2  2
                          +  t  t
                          w  +  +
                             w  w
    */

    computeLandmarks() : BoxLandmarks {
        const ax = 0;
        const b1x = ax + this.props.height;
        const b2x = b1x + this.props.thickness;
        const b3x = b2x + this.props.thickness;
        const cx = b2x + this.props.height;
        const c1x = cx + this.props.thickness;
        const a1x = Math.max(ax, cx - 2 * this.props.length / 3);
        const dx = cx + this.props.width;
        const d1x = dx - this.props.thickness;
        const e1x = dx + this.props.height;
        const e2x = e1x + this.props.thickness;
        const e0x = e1x - this.props.thickness;
        const fx = e2x + this.props.height;
        const f1x = Math.min(fx, dx + 2 * this.props.length / 3);

        const gy = 0;
        const h1y = gy + this.props.height;
        const h2y = h1y + this.props.thickness;
        const iy = h2y + this.props.height;
        const g1y = Math.max(0, iy - this.props.width / 2);
        const jy = iy + this.props.length;
        const ky = jy + this.props.height;
        const ly = ky + this.props.length - this.props.thickness;
        const my = ly + this.props.height;

        return {
            ax: ax, a1x: a1x, b1x: b1x, b2x: b2x, b3x: b3x, cx: cx, c1x: c1x, dx: dx, d1x: d1x, e0x: e0x, e1x: e1x, e2x: e2x, fx: fx, f1x: f1x,
            gy: gy, g1y: g1y, h1y: h1y, h2y: h2y, iy: iy, jy: jy, ky: ky, ly: ly, my: my,
            rotate: f1x > my
       }
    }

    renderSvg(display: boolean) {
        const landmarks = this.computeLandmarks();

        const tab_width = this.props.width / 6;
        const tab_depth = 1.5 * this.props.thickness;
        const left_center = landmarks.cx + this.props.width / 4
        const right_center = landmarks.dx - this.props.width / 4;
        const tab_length = this.props.length / 6;
        const front_center = landmarks.iy + this.props.length / 4;
        const back_center = landmarks.jy - this.props.length / 4;

        const middle = (landmarks.cx + landmarks.dx) / 2;

        const ix = (landmarks.b2x + landmarks.cx) / 2;
        const iy = landmarks.g1y;
        const outline = [
            ix, iy,
            landmarks.cx, landmarks.g1y,
            landmarks.cx, landmarks.iy,
            landmarks.cx, landmarks.gy,
            left_center - tab_width / 2 - tab_depth / 2, landmarks.gy,
            left_center - tab_width / 2, landmarks.gy - tab_depth,
            left_center + tab_width / 2, landmarks.gy - tab_depth,
            left_center + tab_width / 2 + tab_depth / 2, landmarks.gy,
            right_center - tab_width / 2 - tab_depth / 2, landmarks.gy,
            right_center - tab_width / 2, landmarks.gy - tab_depth,
            right_center + tab_width / 2, landmarks.gy - tab_depth,
            right_center + tab_width / 2 + tab_depth / 2, landmarks.gy,
            landmarks.dx, landmarks.gy,
            landmarks.dx, landmarks.iy,
            landmarks.dx, landmarks.g1y,
            landmarks.e1x, landmarks.g1y,
            landmarks.e1x, landmarks.iy,
            landmarks.fx, landmarks.iy,
            landmarks.fx, front_center - tab_length / 2 - tab_depth / 2,
            landmarks.fx + tab_depth, front_center - tab_length / 2,
            landmarks.fx + tab_depth, front_center + tab_length / 2,
            landmarks.fx, front_center + tab_length / 2 + tab_depth / 2,
            landmarks.fx, back_center - tab_length / 2 - tab_depth / 2,
            landmarks.fx + tab_depth, back_center - tab_length / 2,
            landmarks.fx + tab_depth, back_center + tab_length / 2,
            landmarks.fx, back_center + tab_length / 2 + tab_depth / 2,
            landmarks.fx, landmarks.jy,
            landmarks.dx, landmarks.jy,
            landmarks.f1x, landmarks.jy,
            landmarks.f1x, landmarks.ky,
            landmarks.d1x, landmarks.ky,
            landmarks.e0x, landmarks.ky,
            landmarks.e0x, landmarks.ly,
            landmarks.d1x, landmarks.ly,
            landmarks.d1x, landmarks.my,
            landmarks.c1x, landmarks.my,
            landmarks.c1x, landmarks.ly,
            landmarks.b3x, landmarks.ly,
            landmarks.b3x, landmarks.ky,
            landmarks.c1x, landmarks.ky,
            landmarks.a1x, landmarks.ky,
            landmarks.a1x, landmarks.jy,
            landmarks.cx, landmarks.jy,
            landmarks.ax, landmarks.jy,
            landmarks.ax, back_center + tab_length / 2 + tab_depth / 2,
            landmarks.ax - tab_depth, back_center + tab_length / 2,
            landmarks.ax - tab_depth, back_center - tab_length / 2,
            landmarks.ax, back_center - tab_length / 2 - tab_depth / 2,
            landmarks.ax, front_center + tab_length / 2 + tab_depth / 2,
            landmarks.ax - tab_depth, front_center + tab_length / 2,
            landmarks.ax - tab_depth, front_center - tab_length / 2,
            landmarks.ax, front_center - tab_length / 2 - tab_depth / 2,
            landmarks.ax, landmarks.iy,
            landmarks.b2x, landmarks.iy,
            landmarks.b2x, landmarks.g1y,
            ix, iy
        ];
        const path = kHelper.pathFromOutline(kHelper.bluntCorners(outline, tab_depth));

        const notch = this.props.width / 16;

        const fill = display ? '#eee' : 'none'
        const anti_fill = display ? '#fff' : 'none'

        const cuts = (
            <g stroke="red" strokeWidth="0.01" fill="none">
                <path d={path} fill={fill} />
                <rect fill={anti_fill} x={left_center - tab_width / 2 - tab_depth / 2} y={landmarks.iy} width={tab_width + tab_depth} height={this.props.thickness}/>
                <rect fill={anti_fill} x={right_center - tab_width / 2 - tab_depth / 2} y={landmarks.iy} width={tab_width + tab_depth} height={this.props.thickness}/>
                <rect fill={anti_fill} x={landmarks.cx} y={front_center - tab_length / 2 - tab_depth / 2} width={tab_depth} height={tab_length + tab_depth} />
                <rect fill={anti_fill} x={landmarks.dx - tab_depth} y={front_center - tab_length / 2 - tab_depth / 2} width={tab_depth} height={tab_length + tab_depth} />
                <rect fill={anti_fill} x={landmarks.cx} y={back_center - tab_length / 2 - tab_depth / 2} width={tab_depth} height={tab_length + tab_depth} />
                <rect fill={anti_fill} x={landmarks.dx - tab_depth} y={back_center - tab_length / 2 - tab_depth / 2} width={tab_depth} height={tab_length + tab_depth} />
                <rect fill={anti_fill} x={middle - this.props.width / 16} y={landmarks.h1y - tab_depth} width={this.props.width / 8} height={2*tab_depth + this.props.thickness} />
                <rect fill={anti_fill} x={middle - this.props.width / 16} y={landmarks.h1y - tab_depth} width={this.props.width / 8} height={2*tab_depth + this.props.thickness} />
                <path d={`M ${middle - notch} ${landmarks.ly} A ${notch} ${notch} 180 1 0 ${middle + notch} ${landmarks.ly}`} />
            </g>
        )

        const scores = (
            <g stroke="black" strokeWidth="0.01" fill="none" strokeDasharray="0.05 0.05">
                <line x1={landmarks.cx} y1={landmarks.h1y} x2={middle - notch} y2={landmarks.h1y} />
                <line x1={middle + notch} y1={landmarks.h1y} x2={landmarks.dx} y2={landmarks.h1y} />
                <line x1={landmarks.cx} y1={landmarks.h2y} x2={middle - notch} y2={landmarks.h2y} />
                <line x1={middle + notch} y1={landmarks.h2y} x2={landmarks.dx} y2={landmarks.h2y} />
                <line x1={landmarks.b2x} y1={landmarks.iy} x2={landmarks.e1x} y2={landmarks.iy} />
                <line x1={landmarks.cx} y1={landmarks.jy} x2={landmarks.dx} y2={landmarks.jy} />
                <line x1={landmarks.c1x} y1={landmarks.ky} x2={landmarks.d1x} y2={landmarks.ky} />
                <line x1={landmarks.c1x} y1={landmarks.ly} x2={middle - notch} y2={landmarks.ly} />
                <line x1={middle + notch} y1={landmarks.ly} x2={landmarks.d1x} y2={landmarks.ly} />
                <line x1={landmarks.cx} y1={landmarks.iy} x2={landmarks.cx} y2={landmarks.ky} />
                <line x1={landmarks.dx} y1={landmarks.iy} x2={landmarks.dx} y2={landmarks.ky} />
                <line x1={landmarks.c1x} y1={landmarks.ky} x2={landmarks.c1x} y2={landmarks.ly} />
                <line x1={landmarks.d1x} y1={landmarks.ky} x2={landmarks.d1x} y2={landmarks.ly} />
                <line x1={landmarks.b1x} y1={landmarks.iy} x2={landmarks.b1x} y2={landmarks.jy} />
                <line x1={landmarks.b2x} y1={landmarks.iy} x2={landmarks.b2x} y2={landmarks.jy} />
                <line x1={landmarks.e1x} y1={landmarks.iy} x2={landmarks.e1x} y2={landmarks.jy} />
                <line x1={landmarks.e2x} y1={landmarks.iy} x2={landmarks.e2x} y2={landmarks.jy} />
           </g>
        )

        const svg_sizes = BoxHelper.sizesFromPaper(this.props.paperSize, landmarks.rotate);
        const style = display ? {border:'1px solid #888'} : null;

        return (
            <svg {...svg_sizes} style={style}>
                {cuts}
                {scores}
            </svg>
        );
    }
 
 }